import React from 'react';
import { Button } from 'antd';
import './RecipeToolbar.css';

export default class extends React.Component {
    render() {
        return (
            <div className="recipe-toolbar">
                <Button onClick={() => this.props.showModal('add')}>Add</Button>
                <Button disabled={!this.props.selected} onClick={() => this.props.showModal('edit')}>Edit</Button>
                <Button disabled={!this.props.selected} onClick={() => this.props.delete()}>Delete</Button>
            </div>
        );
    }
}