import React from 'react';
import './Planner.css';
import { Carousel, Card, Calendar, Badge, Radio, Timeline, Form, Button, Input, Dropdown, Menu, AutoComplete, Select, Modal, DatePicker, TimePicker, Cascader, InputNumber, Icon, Table, } from 'antd';
import moment from 'moment';
import { validate } from '@babel/types';

export default class extends React.Component {
    state = {
        mode: "list",
        listDays: Number.isInteger(window.foodnet.listDays) ? window.foodnet.listDays : 7,
    }

    render() {
        return (
            <>
                <div className="planner">
                    {this.renderToolbar()}
                    {this.state.mode === "calendar" && this.renderCalendar()}
                    {this.state.mode === "list" && this.renderList()}
                </div>
                {this.renderAddListItemModal()}
            </>
        );
    }

    renderToolbar() {
        return (
            <div className="planner-toolbar">
                <Radio.Group onChange={(e) => this.setState({ mode: e.target.value })} value={this.state.mode}>
                    <Radio value={"list"}>List View</Radio>
                    <Radio value={"calendar"}>Calendar View</Radio>
                </Radio.Group>
                {this.state.mode === "list" && (
                    <Input type="number" value={this.state.listDays} onChange={e => {
                        if (e.target.value.length === 0) {
                            this.setState({ listDays: undefined })
                        }
                        else {
                            let v = Number(e.target.value)
                            if (v < 0) v = 0;
                            this.setState({ listDays: v })
                            window.foodnet.listDays = v
                            window.foodnet.sync()
                        }
                    }} />
                )}
            </div>
        );
    }

    renderAddListItemModal() {
        const valid = () => {
            return this.state.recipe;
        }
        const submit = () => {
            if (this.state.eventid) {
                window.foodnet.events = window.foodnet.events.filter(e => e.id !== this.state.eventid).concat({
                    id: this.state.eventid,
                    date: moment().add(this.state.dayOffset, "days").format(),
                    qty: this.state.qty || 1,
                    recipe: this.state.recipe,
                })
            }
            else {
                window.foodnet.events.push({
                    id: window.foodnet.events.length === 0 ? 1 : Math.max(...window.foodnet.events.map(e => e.id)) + 1,
                    date: moment().add(this.state.dayOffset, "days").format(),
                    qty: this.state.qty || 1,
                    recipe: this.state.recipe,
                })
            }
            window.foodnet.sync();
            this.setState({
                eventid: undefined,
                recipe: undefined,
                qty: undefined,
                dayOffset: undefined,
                modal: undefined,
            })
        }
        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 6 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 12 },
            },
        };
        return (this.state.modal === "add-list-item" || this.state.modal === "edit-list-item") && (
            <Modal
                className="list-item-modal"
                visible={true}
                onCancel={() => this.setState({ modal: undefined })}
                footer={[
                    this.state.modal === "edit-list-item" && (
                        <Button type="danger" onClick={() => {
                            window.foodnet.events = window.foodnet.events.filter(e => e.id !== this.state.eventid);
                            window.foodnet.sync();
                            this.setState({
                                eventid: undefined,
                                recipe: undefined,
                                qty: undefined,
                                dayOffset: undefined,
                                modal: undefined,
                            })
                        }}>Delete Event</Button>
                    ),
                    <Button key="back" onClick={() => this.setState({ modal: undefined })}>Cancel</Button>,
                    <Button key="submit" type="primary" disabled={!valid()} onClick={() => submit()}>OK</Button>,
                ]}
            >
                <Form {...formItemLayout}>
                    <Form.Item validateStatus={valid() ? "success" : "error"} hasFeedback={true} label="Recipe">
                        <Select value={this.state.recipe} onChange={v => this.setState({ recipe: Number(v) })}>
                            {window.foodnet.recipes.map(r => (
                                <Select.Option key={r.id} value={r.id}>{r.name}</Select.Option>
                            ))}
                        </Select>
                    </Form.Item>
                    <Form.Item label="Quantity">
                        <Input
                            autoFocus
                            type="number"
                            defaultValue={1}
                            value={this.state.qty}
                            onChange={e => this.setState({ qty: e.target.value })}
                            onPressEnter={() => {
                                if (valid()) {
                                    submit();
                                }
                            }}
                        />
                    </Form.Item>
                </Form>
            </Modal>
        )
    }

    renderList() {
        const now_s_format = "Y-M-D";
        const createItemsForDay = (dayOffset) => {
            return window.foodnet.events
            .filter(e => {
                return moment().add(dayOffset, "days").format(now_s_format) === moment(e.date).format(now_s_format)
            })
            .sort((a, b) => a.id < b.id ? -1 : 1)
            .map(e => (
                <Timeline.Item color="green" onClick={() => {
                    this.setState({
                        eventid: e.id,
                        modal: "edit-list-item",
                        dayOffset: dayOffset,
                        recipe: e.recipe,
                        qty: e.qty,
                    })
                }} >{e.qty} x {window.foodnet.recipes.find(r => r.id === e.recipe).name}</Timeline.Item>
            ))
        }
        const addForDay = (dayOffset) => {
            return <Timeline.Item onClick={() => {
                this.setState({
                    eventid: undefined,
                    modal: "add-list-item",
                    dayOffset: dayOffset,
                    recipe: undefined,
                    qty: undefined,
                })
            }} dot={<Icon type="plus-circle" theme="twoTone" twoToneColor="red" />}>Add</Timeline.Item>;
        }
        const createList = (dayOffset) => {
            return (
                <Timeline key={dayOffset}>
                    <Timeline.Item color="blue"><b><u>{moment().add(dayOffset, 'days').format("dddd (MMM D)")}</u></b></Timeline.Item>
                    {createItemsForDay(dayOffset)}
                    {addForDay(dayOffset)}
                </Timeline>
            )
        }
        return (
            <div className="planner-list">
                {Array.from(Array(this.state.listDays)).map((z, day) => createList(day))}
            </div>
        )
    }

    renderCalendar() {
        function getListData(value) {
            let listData;
            switch (value.date()) {
                case 8:
                    listData = [
                        { type: 'warning', content: 'This is warning event.' },
                        { type: 'success', content: 'This is usual event.' },
                    ];
                    break;
                case 10:
                    listData = [
                        { type: 'warning', content: 'This is warning event.' },
                        { type: 'success', content: 'This is usual event.' },
                        { type: 'error', content: 'This is error event.' },
                    ];
                    break;
                case 15:
                    listData = [
                        { type: 'warning', content: 'This is warning event' },
                        { type: 'success', content: 'This is very long usual event....' },
                        { type: 'error', content: 'This is error event 1.' },
                        { type: 'error', content: 'This is error event 2.' },
                        { type: 'error', content: 'This is error event 3.' },
                        { type: 'error', content: 'This is error event 4.' },
                    ];
                    break;
                default:
            }
            return listData || [];
        }
        function dateCellRender(value) {
            const listData = getListData(value);
            return (
                <ul className="events">
                    {listData.map(item => (
                        <li key={item.content}>
                            <Badge status={item.type} text={item.content} />
                        </li>
                    ))}
                </ul>
            );
        }

        return (
            <Calendar dateCellRender={dateCellRender} />
        );
    }
}