import React from 'react';
import './RecipeList.css';

export default class extends React.Component {
    render() {
        return (<div className="recipe-list">
            {window.foodnet.recipes.sort((a, b) => a.name.toLowerCase() < b.name.toLowerCase() ? -1 : 1).map(r => this.createRecipe(r))}
        </div>)
    }

    createRecipe(r) {
        return (
            <div key={r.id} className={"recipe-list-item" + (window.foodnet.selected === r.id ? " selected" : "")}
                onClick={() => { window.foodnet.selected = r.id; this.props.select(r.id); }}
            >
                {r.name}
            </div>
        )
    }
}