import XLSX from 'xlsx';

export const load = (input) => {
    const wb = XLSX.read(input, { type: 'array' });
    const r = XLSX.utils.sheet_to_json(wb.Sheets.Recipes);
    const i = XLSX.utils.sheet_to_json(wb.Sheets.Ingredients);
    const m = XLSX.utils.sheet_to_json(wb.Sheets.Measurements);
    const e = XLSX.utils.sheet_to_json(wb.Sheets.Events);
    window.foodnet.recipes = r.map(it => ({ ...it, ingredients: JSON.parse(it.ingredientsJson) }));
    window.foodnet.ingredients = i;
    window.foodnet.measurements = m;
    window.foodnet.events = e;
    window.foodnet.sync();
}

export const save = (filename) => {
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, XLSX.utils.json_to_sheet(window.foodnet.recipes.map(it => ({ ...it, ingredientsJson: JSON.stringify(it.ingredients) }))), "Recipes");
    XLSX.utils.book_append_sheet(wb, XLSX.utils.json_to_sheet(window.foodnet.ingredients), "Ingredients");
    XLSX.utils.book_append_sheet(wb, XLSX.utils.json_to_sheet(window.foodnet.measurements), "Measurements");
    XLSX.utils.book_append_sheet(wb, XLSX.utils.json_to_sheet(window.foodnet.events), "Events");
    XLSX.writeFile(wb, filename || 'foodnet.xlsx');
}